import { ApiProperty } from '@nestjs/swagger';

import {
  IMember,
  IProjectAddress,
  IProjectContractStatus,
  IProjectResponse,
  IProjectWorkStatus,
} from '../../application/interface';

export class Member implements IMember {
  @ApiProperty({
    title: 'Project bind ID',
    description: 'UUIDv7',
  })
  project_bind_id: string;

  @ApiProperty({
    title: 'Project ID',
    description: 'UUIDv7',
  })
  project_id: string;

  @ApiProperty({
    title: 'Project_member ID',
  })
  project_member_id: string;
}

export class ProjectAddress implements IProjectAddress {
  @ApiProperty({
    title: 'Project address ID',
    description: 'UUIDv7',
  })
  project_address_id: string;

  @ApiProperty({
    title: 'Project ID',
    description: 'UUIDv7',
  })
  project_id: string;

  @ApiProperty({
    title: 'Project postal code',
  })
  project_postal_code: string;

  @ApiProperty({
    title: 'Project province',
  })
  project_province: string;

  @ApiProperty({
    title: 'Project address',
  })
  project_address: string;
}

export class ProjectContractStatus implements IProjectContractStatus {
  @ApiProperty({
    title: 'Project contract status ID',
    description: 'UUIDv7',
  })
  project_contract_status_id: string;

  @ApiProperty({
    title: 'Is project stop',
  })
  is_project_stop: boolean;

  @ApiProperty({
    title: 'Project ID',
    description: 'UUIDv7',
  })
  project_id: string;

  @ApiProperty({
    title: 'Status name',
  })
  status_name: string;

  @ApiProperty({
    title: 'Status reason',
  })
  status_reason: string;

  @ApiProperty({
    title: 'Status timestamp',
  })
  status_timestamp: string;
}

export class ProjectWorkStatus implements IProjectWorkStatus {
  @ApiProperty({
    title: 'Project work status ID',
    description: 'UUIDv7',
  })
  project_work_status_id: string;

  @ApiProperty({
    title: 'Is project stop',
  })
  is_project_stop: boolean;

  @ApiProperty({
    title: 'Project ID',
    description: 'UUIDv7',
  })
  project_id: string;

  @ApiProperty({
    title: 'Status name',
  })
  status_name: string;

  @ApiProperty({
    title: 'Status reason',
  })
  status_reason: string;

  @ApiProperty({
    title: 'Status timestamp',
  })
  status_timestamp: string;
}

export class ProjectResponse implements IProjectResponse {
  @ApiProperty({
    title: 'Project ID',
    description: 'UUIDv7',
  })
  project_id: string;

  @ApiProperty({
    title: 'Project name',
  })
  project_name: string;

  @ApiProperty({
    title: 'Customer',
  })
  customer_id: string;

  @ApiProperty({
    title: 'Contractor ID', // It will contain an ID from a different system.
  })
  contractor_id: string;

  @ApiProperty({
    title: 'Project work type',
  })
  project_type: string;

  @ApiProperty({
    title: 'Project member',
  })
  project_member: Member;

  @ApiProperty({
    title: 'Construction start',
    format: 'yyyy-MM-dd',
  })
  construction_start: Date;

  @ApiProperty({
    title: 'Construction stop',
    format: 'yyyy-MM-dd',
  })
  construction_stop: Date;

  @ApiProperty({
    title: 'Construction scale',
    required: false,
  })
  construction_scale?: string;

  @ApiProperty({
    title: 'Budget',
    required: false,
  })
  budget?: string;

  @ApiProperty({
    title: 'Address',
    required: false,
  })
  address?: ProjectAddress;

  @ApiProperty({
    title: 'Total man work',
    required: false,
  })
  total_man_works?: string;

  @ApiProperty({
    title: 'Project contract type',
  })
  contract_type: string;

  @ApiProperty({
    title: 'Project construction types',
  })
  construction_types: string[];

  @ApiProperty({
    title: 'Project materials',
  })
  materials: string[];

  @ApiProperty({
    title: 'Image URL',
    required: false,
  })
  image_url?: string;

  @ApiProperty({
    title: 'Description',
    required: false,
  })
  description?: string;

  @ApiProperty({
    title: 'Project contract status',
  })
  contract_status: ProjectContractStatus;

  @ApiProperty({
    title: 'Project work status',
  })
  work_status: ProjectWorkStatus;

  @ApiProperty({
    title: 'Created by user',
  })
  created_user_id: string;
}
